export class ConfirmationDialogueInput {
    message: string;
    buttons: ConfirmationDialogueButtons[];
    closeButtons: string[];
}
export class ConfirmationDialogueButtons {
    text: string;
    color: string;
    returnText: string;
    constructor(text: string, returnText: string, color?: string) {
        this.text = text;
        this.returnText = returnText;
        this.color = color;
    }

}
