import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NotificationService } from 'src/app/theme/notification/notification.service';
import { LoginService } from '../login.service';

@Component({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.css']
})
export class LogoutComponent implements OnInit {

  constructor(private router: Router, private notificationService: NotificationService, private logoutService: LoginService) { }

  ngOnInit() {
    sessionStorage.removeItem('authenticated');
    sessionStorage.removeItem('auth-key');
    this.router.navigate(['login']);
    this.logoutService.logout();
    this.notificationService.sendNotification('Logged out successfully!');
  }

}
