import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ConfirmationDialogueInput } from './confirmation-dialogue-input';

@Component({
  selector: 'app-confirmation-dialogue',
  templateUrl: './confirmation-dialogue.component.html',
  styleUrls: ['./confirmation-dialogue.component.css']
})
export class ConfirmationDialogueComponent implements OnInit {

  constructor(
    private dialogRef: MatDialogRef<ConfirmationDialogueComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ConfirmationDialogueInput) {}

  ngOnInit() {
  }



  onCloseClick(): void {
    this.dialogRef.close('');
  }

  buttonClicked(i: number) {
    this.dialogRef.close(this.data.buttons[i].returnText);
  }
}
