import { environment } from './environment';
import { Injectable } from '@angular/core';
@Injectable({
  providedIn: 'root',
})
export class BackendAPIService {

  private getBaseUrl() {
    const mockEnabled = true;
    const backendUrl = mockEnabled ? environment.backendUrl : environment.mockBackendUrl;
    const PATH_SEPERATOR = '/';
    const context = environment.contextName;

    let baseURL = backendUrl + PATH_SEPERATOR;
    if (context !== '') {
      baseURL = baseURL + context + PATH_SEPERATOR;
    }
    return  baseURL;
  }

  getAccountsAPI(): string {
    return this.getBaseUrl()  + 'user/products/accounts';
  }
  getDepositAccountsAPI(): string {
    return this.getBaseUrl()  + 'user/products/deposits';
  }
  getLoanAccountsAPI(): string {
    return this.getBaseUrl()  + 'user/products/loans';
  }
  getCardsAccountsAPI(): string {
    return this.getBaseUrl()  + 'user/products/cards';
  }
  getCorporatesAPI(): string {
    return this.getBaseUrl() + 'corporates/';
  }

  getLogintAPI(): string {
    return this.getBaseUrl()  + 'auth/login';
  }
  getLogOutAPI(): string {
    return this.getBaseUrl()  + 'logout';
  }
    getInventoryListAPI(pageSize: number, pageNumber: number): string {
      return this.getBaseUrl()  +
       'inventory/list' + '?pageSize=' + pageSize + '&pageNumber=' + pageNumber + '&sortBy=id';
    }
    getUpdateInventoryListAPI(companyName: string, medicineName: string): string {
      return this.getBaseUrl()  +
       'inventory/update/' + companyName + '/' + medicineName + '/' ;
    }
    getSearchInventoryListAPI(searchCriteria: string): string {
      return this.getBaseUrl()  +
      'inventory/search?q=' + searchCriteria ;
    }

  getDeleteArticleAPI(id: number): string {
    return this.getBaseUrl()  + 'article' + '/' + id;
  }
  getCreateArticleAPI(): string {
    return this.getBaseUrl()  + 'article';
  }
  public getArticleListAPI(pageSize: number, pageNumber: number): string {
    return this.getBaseUrl()
      + 'article/list?pageSize='
      + pageSize
      + '&pageNumber='
      + pageNumber
      + '&sortBy=id';
  }
  public getUsersListAPI(pageSize: number, pageNumber: number) {
    return this.getBaseUrl()  + 'user/list?pageSize=' + pageSize + '&pageNumber=' + pageNumber + '&sortBy=id';
  }

}
