import { Injectable } from '@angular/core';
import { User } from '../users/user.model';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BackendAPIService } from 'src/environments/backendAPIs';
import { DataResponse } from 'src/app/common/responseTypes/data-response.model';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  isAuthenticated(): boolean {
    return  sessionStorage.getItem('authenticated') === 'true';
  }
  constructor(private http: HttpClient,
              private backendAPIs: BackendAPIService) { }

  login(user: User): Observable<DataResponse> {
    const authKey = 'Basic ' + btoa(user.username + ':' + user.password);
    const headers = new HttpHeaders(user ? {
      authorization : authKey
  } : {});
    sessionStorage.setItem('auth-key', authKey);
    return this.http.post<DataResponse>(this.backendAPIs.getLogintAPI(), user, {headers});
  }
  logout() {
    const authKey = sessionStorage.getItem('auth-key');
    const headers = new HttpHeaders(authKey ? {
      authorization : authKey
  } : {});
    return this.http.post<DataResponse>(this.backendAPIs.getLogOutAPI(), null, {headers});
  }
}
