import { Component, OnInit } from '@angular/core';
import { AccountsService } from './accounts.service';
import {Account} from './account.model';
import { Deposit } from './deposit.model';
import { Loans } from './loans.model';
import { Card } from './card.model';

@Component({
  selector: 'app-portfolio-summary',
  templateUrl: './portfolio-summary.component.html',
  styleUrls: ['./portfolio-summary.component.css']
})
export class PortfolioSummaryComponent implements OnInit {

  constructor(private accountsService: AccountsService) { }
  currencySymbol: string;
  savingsAccounts: Account[];
  depositAccounts: Deposit[];
  loans: Loans[];
  cards: Card[];
  accountsTotal = 0;
  totalDueThisMonth = 0;
  totalLoanAmountPending = 0;
  savingsTotal = 0;
  depositsTotal = 0;
  ngOnInit() {
    this.accountsService.getSavingsAccounts().subscribe( (accounts: Account[]) => {
      this.savingsAccounts = accounts;
      this.savingsAccounts.forEach((account) => {
        this.accountsTotal += account.balance;
        this.savingsTotal += account.balance;
      });
    });

    this.accountsService.getDepositAccounts().subscribe( (depositAccounts: Deposit[]) => {
      this.depositAccounts = depositAccounts;
      this.depositAccounts.forEach((account) => {
        this.accountsTotal += account.balance;
        this.depositsTotal += account.balance;
      });
    });
    this.accountsService.getLoanAccounts().subscribe( (loans: Loans[]) => {
      this.loans = loans;
      this.loans.forEach((account) => {
        this.totalDueThisMonth += account.monthlyEmi;
        this.totalLoanAmountPending += (account.initialAmount - account.paidAmount);
      });
    });
    this.accountsService.getCardAccounts().subscribe( (cards: Card[]) => {
      this.cards = cards;
      this.cards.forEach((card) => {
        if (card.balance < 0) {
          this.totalDueThisMonth += card.balance;
        }
      });
    });
  }

}
